module.exports = "/* Style the tab */\n.tab {\n    overflow: hidden;\n  }\n/* Style the buttons inside the tab */\n.button {\n    background-color: rgb(230, 187, 187);\n    float: left;\n    border: none;\n    outline: none;\n    cursor: pointer;\n    padding: 8px 10px;\n    transition: 0.3s;\n    font-size: 14px;\n    font-style:underline;\n    border:solid;\n    border-color:gray;\n    border-top-left-radius: 15px;\n    border-top-right-radius: 15px;\n  }\n/* Change background color of buttons on hover */\n.button:hover {\n    background-color: rgb(231, 154, 154);\n  }\n.buttonSel {\n    background-color: inherit;\n    float: left;\n    border: none;\n    outline: none;\n    cursor: pointer;\n    padding: 8px 10px;\n    transition: 0.3s;\n    font-size: 14px;\n    font-style:underline;\n    font-style:bold;\n    border:solid;\n    border-color:gray;\n    border-top-left-radius: 15px;\n    border-top-right-radius: 15px;\n  }\n/* Change background color of buttons on hover */\n.buttonSel:hover {\n    background-color: rgb(231, 154, 154);\n  }\n/* Create an active/current tablink class */\n.button.active {\n    background-color: rgb(97, 89, 89);\n    font-style:underline;\n  }\n/* Style the tab content */\n.tabcontent {\n    display: none;\n    padding: 6px 12px;\n    border: 1px solid #ccc;\n    border-top: none;\n  }\n \n/*# sourceMappingURL=data:application/json;base64, */"